










import Vue from 'vue'
import Page from '@/components/Page.vue'

export default Vue.extend({
  components: { Page },
  methods: {
    show(value: any): void {
      const config = {
        type: value,
        title: '这是一个标题，可以为一行或者两行。',
      }
      const menus = [
        { text: '示例菜单' },
        { text: '示例菜单' },
        { text: '负向菜单', type: 'warn' },
      ]
      this.$actionSheet.show(config, menus).then((res: any) => {})
    },
  },
})
